<template>
    <div class="all">
        
       <ul class="head">
           <li>最新电影</li>
           <li>最受好评</li>
           <li>最热</li>
           <li @click="show1()">分类</li>
       </ul>
       <!-- 隐藏的分类 -->
      <div class="sort-all">
           <ul class="sort-1">
              <li>电影</li>
              <li>电视剧</li>
              <li>综艺</li>
              <li>动漫</li>
              <li>游戏</li>
              <li>直播</li>
              <li>收听</li>
              <li>电影</li>
              <li>电视剧</li>
              <li>动漫</li>
              <li>游戏</li>
              <li>直播</li>
              <li>收听</li>
              <li>电影</li>
              <li>电视剧</li>
              <li>综艺</li>
         
         
           </ul>
      </div>
      
      <!-- 电影列表 -->
       <div class="app-goodlist" v-for="item in list" :key="item.id">
     <!-- 左边 -->
     <div class="goods-item" @click="link($event)">
        
          <img :src="item.img_url">
       <!-- <h4>豆瓣8.0分</h4>  -->
         <div class="info">
             <span class="now"></span>
         </div>
         <div class="sell">
             <span>{{item.name}}</span>
         </div>
     </div>
     <!-- 右 -->
       <div class="goods-item" @click="link($event)">
        
          <img :src="item.img_url">
         <!-- <h4>{{item.title}}</h4> -->
         <div class="info">
             <span class="now"></span>
         </div>
         <div class="sell">
             <span>{{item.name}}</span>
         </div>
     </div>
     </div>
     <!-- list2 -->     
      <mt-button type="primary" size="large" @click="loadMore()">加载更多</mt-button>
    
    </div>
</template>
<script>
import {show} from "../../fengzhuang/function.js"
export default {
    data(){
        return {list:[{name:"阿丽塔：战斗天使",img_url:"./static/img/alt.jpg"},
        {name:"卡夫",img_url:"./static/img/animal.jpg"},{name:"流浪地球",img_url:"./static/img/earth.jpg"},
        {name:"掠食城市",img_url:"./static/img/city.jpg"},
        {name:"阿丽塔",img_url:"./static/img/alt.jpg"},
        {name:"卡夫",img_url:"./static/img/alt.jpg"},{name:"流浪地球",img_url:"./static/img/earth.jpg"},
        {name:"掠食城市",img_url:"./static/img/city.jpg"}],
        list2:[]
        }
    },
    methods:{
        //显示与隐藏
        show1(){
            //先定义个函数
      
        //在调用
            var sort=document.getElementsByClassName("sort-all")[0]
            show(sort)
        },
       //跳转到MovieInfo
       link(event){
           event.preventDefault()
           var n= event.target
           console.log(n)
           console.log(n.parentElement)
           let movieName = n.parentElement.lastElementChild.firstElementChild.innerHTML
         console.log(movieName)
         //获取全局
           this.$store.commit("movieNames",movieName)
        //    this.$store.commit("mN",n)
           this.$router.push("/MovieInfo")
       

       },
      /*  loadMore(){
           var url = "https://api.apiopen.top/videoCategory"
           this.axios.get(url)
           .then(result=>{
       console.log(result.data.result.itemList)
      var result2 =  result.data.result.itemList
           var row=this.list2.concat(result2)
    //   赋值
    this.list2=row
           })
       } */

     },
     created(){}
        
    
}
</script>
<style>
   
    .all input{display: block ;width:70%;}
    ul{list-style: none;display:block}
   .head{display: inline-flex;flex:row;width:100%;height:53px;margin:0 0;
   background-color: rgb(113, 117, 312)}
   .head li{display:block; font-size: 17px;word-break: break-all;text-align: center;
   margin:0px 6px;margin-top:7px}
   .head li:first-child{margin-left:-20px}
   .head li:last-child{margin-left:134px;font-family:fantasy;padding:0 0}
   /* 分类部分 */
       .sort-all{
           display:none;width:100%;height:170px;border-bottom: 2px solid #caa;
       border-top: 2px solid #caa}
       .sort-all ul{display: flex;flex-wrap: wrap;margin:13px -29px}
       .sort-all ul li{word-break: break-all;margin:2px 9px;
       width:57px;height:28px;background-color: #ccc;border-radius: 36%;
       text-align:center;padding:6px 0}




   /* 商品列表 */
   .app-goodlist{display:flex;flex-direction:row;flex-wrap: wrap;
 justify-content:space-between;/* 2端对齐*/
    padding: 4px} 
    /* 左边 */
.app-goodlist .goods-item{
    border:2px solid #faa;width:49%;
    box-shadow:0 0 8px #ccc;margin:4px 0;
    padding:2px;
    display:flex;
    flex-direction:column;/* 子元素垂直排列 */
    min-height:230px    /* 最小高度 */
    }  
    .app-goodlist .goods-item img{
         width:100% /*使其图片显示完整 */
    }
    .app-goodlist .goods-item h4{
        font-size:18px
    }
    .app-goodlist .goods-item .now{
        color:red;
        font-size: 16px;
        font-weight: bold
   } 
 .sell{width:100%;background-color: black}
 .sell span{display:block;color:#fff;text-align: center;width:100%;font-size:13px}

</style>



