<template>
<div class="app-login">
    <h5>登录</h5>
    <form>
    用户名：<input type="text" name="uname" v-model="uname">
    密码：<input type="password" name="upwd" v-model="upwd">
    <input type="button" value="登录" @click="butLogin">
    </form>
</div>
</template>

<script>
import {Toast} from "mint-ui"
export default {
    data(){return {uname:"",upwd:""}},
    methods:{
        butLogin(){
            console.log(123)
            //获取用户输入用户名和密码
         var u=this.uname
         var p=this.upwd
         console.log(u+"-"+p)
         //验证不能为空
         var reg = /^[a-z0-9]{3,12}$/
         if(!reg.test(u)){
             Toast("用户名不能为空,采用游客登录")

             return
         }
         //以下是游客登录
         else{Toast("登录成功")
              this.$router.push("/Home")
              this.$store.commit("changeName",u)}
       //发送ajax请求
    /*       var url = "http://127.0.0.1:3000/"
         url+="login?uname="+u+"&upwd="+p
         this.axios.get(url).then(result=>{
             console.log(result.data.code)
             //吐过登录正确，跳转到主页
             if(result.data.code==-1){
                 Toast("登录失败")
            
             }
             else {Toast("登录成功")
              this.$router.push("/Home")}
         }) */  
        }
    }
}
</script>

<style>

</style>



