<template>
     <div>
<div class="mui-card">
				<div class="mui-card-header mui-card-media" :style="{'height:40vw;background-image':'url('+item+')'}"></div>
				<div class="mui-card-content">
                    <img :src="item" alt="" class="img-1">
					<div class="mui-card-content-inner">
						<p>Posted on January 18, 2016</p>
						<p style="color: #333;">这里显示文章摘要，让读者对文章内容有个粗略的概念...</p>
					  
                    </div>
				</div>
				<!-- 评论列表 -->
          <div class="mui-card-footer" v-for="(tem,i) in list2" :key="tem.id2">
                    <div class="cmt-list">
                        <div class="cmt-itm">
                            <div>第{{i+1}}楼 发表时间：{{tem.data.releaseTime | datatimeFilter}}</div>
                        <div>{{tem.data.title}}</div>
                        </div>
                    </div>
                    </div>
                <!-- 评论 -->
                    	<div class="mui-card-header">回复</div>
				<div class="mui-card-content">
					<div class="mui-card-content-inner">
					<textarea placeholder="请输入内容" v-model="msg">
                    </textarea>
                   <mt-button type="primary" size="large" @click="send">
                        发表评论
                        </mt-button>
                        </div>
                         	
				</div>
			</div>
            

     </div>
</template>
<script>
import {Toast} from "mint-ui"
export default {
    data(){
        return {list2:[],item:[],msg:""}
    },
    methods:{
          loadMore(){
           var url = "https://api.apiopen.top/videoRecommend?id=127398"
           this.axios.get(url)
           .then(result=>{
       console.log(result.data.result)
       console.log(result.data)
      var result2 =  result.data.result
           var row=this.list2.concat(result2)
    //   赋值
    this.list2=row
   
    this.item = result2[3].data.author.icon
           })
       },
       send(){
           var msg1 = this.msg
           //stringify 将 js对象转换查询字符串
    /*    var postData = this.qs.stringify({
          nid:5,
          content:this.msg
        });
           console.log(msg1)
            var url = "http://127.0.0.1:3000/";
            url+="addcomment";
           this.axios.post(url,postData)
           .then(result=>{
              console.log(result) 
           })  */
           Toast("发表成功")}
    },
    created(){
        this.loadMore()
    }
}
</script>
<style>
.img-1{text-align: center;margin-left:20%}
</style>




