import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex)
var store = new Vuex.Store({
    //保存全局
    state:{
     loginName:"未登录" || changeName(state,uname),
    //  sessionStorage.getItem("loginName")||"未登录"
    //movieInfo
      movieName:"未知" || movieNames(state,name),
    },
    //修改全局
    mutations:{
        changeName(state,uname){
            state.loginName = uname
            // sessionStorage.setItem("loginName",uname)
        },
        //当点击电影时候修改值，并且跳转到那个页面
           movieNames(state,name){
                  state.movieName = name
           }

    },
    //获取全局的数据
    getters:{
            optCartCount:function(state){
                return state.loginName
            },
            //电影名
             getMovieName:function(state){
              return state.movieName
             }
    }
})

//导出
export default store;
//最后mian.js中添加store




