<template>
    <div class="app-home">
   <!-- 顶部导航 加fixed置顶-->

       
   
   <!-- 过客的页面ul -->
      <!-- <div class="app-home-title">
		  <ul>
			<li class="diyi"><a href="" class="f-weight">全部</a></li>
		  <li><router-link to="/Login">{{$store.getters.optCartCount}}</router-link></li>
          <li><router-link to="/Movie">电影</router-link></li>
		  <li><router-link to="/lifelist">奇趣</router-link></li>
		  <li><router-link to="/lifelist">科技</router-link></li>
		  <li><router-link to="/lifelist">美食</router-link></li>
          <li><router-link to="/lifelist">生活</router-link></li>
          <li><router-link to="/lifelist">动物</router-link></li>
          <li><router-link to="/Movie">电影</router-link></li>
          <li><a href="">其他</a></li>
          </ul>
	  </div> -->
   <!-- 轮播图 -->
   <mt-swipe :auto="3000">
       <mt-swipe-item :key="item.id" v-for="item in list">
           
           <img :src="item.img_url">
       </mt-swipe-item>
   </mt-swipe>
   <!-- 九宫格 这里不写，写自己的 -->

  <!-- 底部 -->
     
<!-- 第n个 -->
<div class="d-list" v-for="item in list" :key="item.id" @click="push($event)">
        <!-- 左边 -->
         <div class="d0">
             <div class="d0-left">
           <h3>{{item.msg}}
           </h3>  
           <p>{{item.content}}</p>
           </div>
         <!-- 右边 -->
         <div class="r-img">
             <img :src="item.img_url" alt="">
         </div>
         </div>
         <!-- 最底部 -->
         <div class="d0-bottom">
            <div >{{item.title}}</div>
            <div><span>{{item.n}}</span>评论</div>
         </div>
          
    </div>
           <h3 @click="loadMore($event)" class="dmore">点击加载更多..</h3>
    <!-- list2 -->

    <div class="d-list" v-for="tem in list2" :key="tem.i"  @click="push($event)">
        <!-- 左边 -->
         <div class="d0">
             <div class="d0-left">
           <h3>{{tem.data.description}}
           </h3>  
           <p>{{tem.data.description}}</p>
           </div>
         <!-- 右边 -->
         <div class="r-img">
             <img :src="tem.data.icon" alt="">
         </div>
         </div>
         <!-- 最底部 -->
         <div class="d0-bottom">
            <div >{{tem.data.title}}</div>
            <div><span>{{tem.data.id}}</span>评论</div>
         </div>
    </div>
    </div>
 
</template>

<script>
export default {
   data(){
       return {//里面方数组放图片
                        list:[{n:"20",title:"奇趣",img_url:"static/img/g1.jpg",msg:"侯世达：我讨厌“人工智能”这个词，因为它们没有智能",
                        content:"果壳的世界是怎么形成的"},
                        {n:"4",title:"生活",img_url:"static/img/g2.jpg",msg:"喉咙经常不舒服？小心咽炎作祟！",
                        content:"长颈鹿不仅有独特的外表，还有独特的习性，特别是雄性长颈鹿，它们真正关心的只有两件事：打架、做不可描述的事……"},
                        {n:"1000",title:"电影",img_url:"static/img/alt.jpg",msg:"首枚私人探测器奔向月球，谷歌月球大奖赛遗孤有成",
                        content:"阿丽塔袭击票房，流浪地球实现赶超"},
                       {n:"24",title:"小组",img_url:"static/img/g4.jpg",msg:"基因编辑到底会不会脱靶？中国科学家用最新研究给出了解答",
                       content:"它们没有智能，还有独特的习性"}],
                list2:[]
}
   },
   methods:{ push(event){
        let v = event.currentTarget
        console.log(v)
        let name = v.querySelector(".d0-bottom div:first-child").innerHTML
      console.log(name)
        if(name == "电影"){
         
            this.$router.push("/Movie")
        }else{
            this.$router.push("/LifeList")
        }
   },
     loadMore(e){
         let more =  event.currentTarget
         e.preventDefault()
         more.style.display="none"
           var url = "https://api.apiopen.top/videoCategory"
           this.axios.get(url)
           .then(result=>{
       console.log(result.data.result.itemList)
      var result2 =  result.data.result.itemList
           var row=this.list2.concat(result2)
    //   赋值
    this.list2=row
           })
       },

   },
   created(){}
}

</script>
<style>
 /* 轮播 */
   .app-home .mint-swipe{height:200px;width:100%}
    .mint-swipe img{height:99%;width:100%}
     /* ul中*/
     /* .app-home .app-home-title{background:#eee; overflow:hidden;
     overflow-x:scroll;}
     .app-home ul{display:inline-flex;
    flex:1;
     overflow:hidden;list-style:none;}
	 .app-home ul li{padding:0 15px 0 20px;word-break: keep-all}
   .app-home ul .diyi{margin-left:-37px}
     .app-home ul li .f-weight{font-weight:999;} */
   /* 底部 */ 
   .app-home.mui-bar{background-color:red }
   .d-list{height: 150px;text-align: center;
       display:flex;border-bottom:3px solid #fff;flex-direction: column}
   .d-list .d0{display:flex;flex:row}

   .d-list .d0 h3{margin-top:23px;letter-spacing: 2px;width:189px;margin-left: 19px;
  overflow: hidden; /*自动隐藏文字*/
text-overflow: ellipsis;  -webkit-box-orient: vertical;
  display: -webkit-box;-webkit-line-clamp: 2;/*! autoprefixer: off */
   font-size:21px;font-weight: bolder; line-height: 1.3 }
   .d-list p{color:#999;font-size:8px;margin-top:8px;margin-left: 22px;width:100px;
    -webkit-box-orient: vertical;overflow: hidden;
  display:-webkit-box;-webkit-line-clamp: 1;  /*! autoprefixer: off */}

   .r-img   {width:109px;    margin: 17px 3px 2px 1px;height: 89px;}

   .r-img img{width:99px;height:80px}

   .d-list .d0-bottom{display:inline-flex;flex:row;margin-bottom:10px;margin-left:4px}
   .d-list .d0-bottom div{    margin-top: 4px;
    font-size: 5px;
    color: grey;}
    .d-list .d0-bottom div:first-child{margin-left:43px}
    .d-list .d0-bottom div:last-child{margin-left:168px}
    .d-list .d0-bottom div span{    font-size: 16px;
    color: #000;
   
    padding-right: 10px;}
   /* 首页 */
  .dmore{margin-top:20px;margin-left: 100px;font-size:1.5rem;color:rgb(174, 204, 170)}
</style>



