<template>
 <div class="app-container" @click="headStyleChange1()">
     <!-- 添加公共组件 -->
   
    <mt-header title="过客知" fixed>
  <router-link to="/home" slot="left">
    <mt-button >首页</mt-button>
  </router-link>
  <mt-button slot="right" @click="search()">搜索</mt-button>
</mt-header>
<div class="search" >
    <input type="text" placeholder="将夜">
</div>
  <!-- 过客的页面ul -->
      <div class="app-home-title">
		  <ul>
			<li class="diyi"><router-link to="/Home" class="f-weight">全部</router-link></li>
		  <li><router-link to="/Login">{{$store.getters.optCartCount}}</router-link></li>
          <li><router-link to="/Movie">电影</router-link></li>
		  <li><router-link to="/lifelist">奇趣</router-link></li>
		  <li><router-link to="/Shoplist">购物车</router-link></li>
		  <li><router-link to="/lifeFood">美食</router-link></li>
          <li><router-link to="/lifelist">生活</router-link></li>
          <li><router-link to="/animal">动物</router-link></li>
          <li><router-link to="/Movie">电影</router-link></li>
          <li><a href="">其他</a></li>
          </ul>
	  </div>
   <!-- 占位符是为了被替换为其他组件 -->
    <router-view></router-view>
    <!-- 添加公共组件 -->
    
 </div>
</template>
<script>
import {headStyleChange,show} from "./fengzhuang/function.js"
export default {
    data(){ return {}},
    methods:{
   search(){
          //怎样使其点击一下之后点击再隐藏。先定义个函数
          
        //在调用
       var search=document.getElementsByClassName("search")[0]
      show(search)
       },
    headStyleChange1(e){
     var s = event.currentTarget
    
     s = s.getElementsByClassName("f-weight")[0]
     .parentElement.parentElement.children
      console.log(s)
       console.log(typeof(s))
       let ys = ""
      for(var t of s){
        //给t li的字体的样式清0
       t.style.fontSize = 16 +"px"
        t.style.fontWeight= 1
 // t.baseURI获得当前地址
      let number = t.baseURI.indexOf("#",0)
       ys = t.baseURI.slice(number+1)
        console.log(ys)}
        if(ys == "/Home" | ys =="/home"){
          t = s[0]
        }else if(ys == "/Login"){t = s[1]} 
        else if(ys == "/Movie" | ys == "/MovieInfo"){t = s[2]} 
        else if(ys == "/Shoplist"){t = s[4]} 
       else if(ys == "/lifelist" | ys=="/LifeDetail"){t = s[3]} 
       else if(ys == "/lifeFood"){t = s[5]}
       else if(ys == "/"){t= s[9]}
       else{t = s[7]}
        console.log(t)
      
      //调用函数开始改变字体大小
      //  headStyleChange() 
        console.log(location.href)
      let str =location.hash
     console.log(str)
      str = str.slice(1)
    //   查找对应的元素
 
     // 
       if(str==ys){
         console.log(t)
         t.style.fontSize = 20 +"px"
         t.style.fontWeight= 999
       }
      }
    },
    created(){}
}
</script>


<style>
/* 设置最大宽度 */

   .app-container{
     padding-top:40px;
     padding-bottom:50px;
     overflow-x:hidden;
   }

 .mui-bar-tab .mui-tab-item-tao.mui-active {
    color: #007aff;
 }
.mui-bar-tab .mui-tab-item-tao {
    display: table-cell;
    overflow: hidden;
    width: 1%;
    height: 50px;
    text-align: center;
    vertical-align: middle;
    white-space: nowrap;
    text-overflow: ellipsis;
    color: #929292;
}
.mui-bar-tab .mui-tab-item-tao .mui-icon {
    top: 3px;
    width: 24px;
    height: 24px;
    padding-top: 0;
    padding-bottom: 0;
}

.mui-bar-tab .mui-tab-item-tao .mui-icon~.mui-tab-label {
   font-size:11px;
   display:block;
   overflow:hidden;
   text-overflow:ellipsis;

}
  
  .search {display:none;width:100%;background-color: antiquewhite;
           }
            .app-home-title{background:#eee; overflow:hidden;
     overflow-x:scroll;}
     .app-home-title ul{display:inline-flex;
    flex:1;
     overflow:hidden;list-style:none;}
	 .app-home-title ul li{padding:0 15px 0 20px;word-break: keep-all}
   .app-home-title ul .diyi{margin-left:-37px}
     .app-home-title ul li .f-weight{font-weight:999;}
</style>



