import Vue from 'vue'
import App from './App.vue'
import router from './router'
//5 引入axios库
import axios from "axios"
//6 配置跨域访问保存session
axios.defaults.withCredentials = true
//7 将axios库配置vue实例对象中
Vue.prototype.axios = axios

// import 'lib-flexible'
//引入vuex
import store from "./store/store.js"
//引入qs使其调用post
// 7.1 s使用post需加载第三方的模块qs
import qs from "qs"
//7.2 配置qs模
Vue.prototype.qs=qs

//引入mint-ui样式文件
import'mint-ui/lib/style.css'

//2 引入mui库样式文件
import './lib/mui/css/mui.css'
import './lib/mui/css/icons-extra.css'
Vue.config.productionTip = false

//3引入mint-ui组件Header
  import {Header,Swipe,SwipeItem,Search,Button} from "mint-ui"
//4 注册Header组件
//Header.name mt-header
Vue.component(Header.name,Header)
Vue.component(Swipe.name,Swipe)
Vue.component(SwipeItem.name,SwipeItem)
Vue.component(Search.name, Search)
Vue.component(Button.name, Button)
//创建日期的过滤器
Vue.filter("datatimeFilter",function(val){
  //创建日期对象；获取年 月 日 时 分 秒
  var date = new Date(val)
  var y = date.getFullYear();//获得年
  var m = date.getMonth()+1;//获得月
  var d = date.getDate()
  var h = date.getHours()
  var mi = date.getMinutes()
  var s = date.getSeconds()
   //返回字符串y-m-d h:min:s
   m<10&&(m="0"+m)//表示如果m<10就执行后面的
   d<10&&(d="0"+d)
   return `${y}-${m}-${d} ${h}:${mi}:${s}`
 })

new Vue({
  router,
  render: h => h(App),
   store,
}).$mount('#app')



