<template>
    <div class="app-card">
        <div class="mui-card">
        <div class="mui-card-header">
            <h3>我的购物车</h3>
            <input type="checkbox" @click="selectAll" :checked="allcb">全选
        </div>
        <div class="mui-card-content">
            <div class="mui-card-content-inner">
              <ul class="mui-table-view">
				<li class="mui-table-view-cell mui-media" v-for="(item,i) in list" :key="item.id">
					<a href="javascript:;">
						<img class="mui-media-object mui-pull-right" src="">
						<div class="mui-media-body">
							
							<p class='mui-ellipsis'> 
                                <input type="checkbox" :checked="item.cb" :data-i="i" @click="modifyItem">{{item.name}}{{item.width}}
                                <button @click="delItem"  :data-id="item.id">删除</button>
                                 </p>
                                 
						</div>
					</a>
				</li>
                </ul>
               </div>
               <button @click="removeItem">删除所选</button>
            <div class="mui-card-footer">小计：120</div>
          </div>
        </div>
    </div>
</template>
<script>
import {Toast} from "mint-ui"
export default {
    data(){
        return {list:[],cb:false,allcb:false}
    },
    methods:{
        //与全选有关，
        loadMore(){
            //1:创建变量保存url  ShopCart.vue
     /*   var url = "http://127.0.0.1:3000/";
        url+="cartlist?uid=1";
        //2:发送ajax请求
        this.axios.get(url).then(result=>{
         //如果没有登录，显示出错销售
        if(result.data.code==-1){Toast("请登录")
        return}

         
         //1:2 没有与 vue data双向绑定 
          //1:接收服务器程序返回数据  19
          var rows = result.data.data;
          //修改全局购物车数量
          this.$store.commit("updateCount",
          rows.length)
          //2:为每一个商品对象添加属性cb 选中状态
          for(var item of rows){
             item.cb = false;
          }
          //3:将最终数组赋值 list 列表    
          this.list = rows;  
        }); */
        // 免费接口部分
        var url = "https://api.apiopen.top/videoRecommend?id=127398"
         this.axios.get(url)
         .then(result=>{
             console.log(result.data.result)
              var rows = result.data.result
              //遍历rows每个值
            for(var row of rows){
                row=row.data.playInfo
                console.log(row)
            }
           
          
           
          if(this.$store.state.loginName=="未登录"){
            Toast("请登录")
        }else{
            
            this.list = row
            
        }
        })
       
      }, 
        //全选
           selectAll(e){
            //    1.获取当前复选框状态
              var cb= e.target.checked 
              console.log(cb)
              //修改全选状态
              this.allcb=cb
               //2:依据全选状态修改数组中cb值
            for(var item of this.list){
                item.cb=cb
                console.log(item.cb)
            }
            //    2 如果状态true
            //    3 将数组中所有cb=true
            //    如果章台false
            //    将数组中所有cb=false
           },
          // 反向全选 ,删除选中的商品
          modifyItem(e){
        //{删除选中商品}
        //1:获取当前元素下标[其中一种方式]9:45
        var idx = e.target.dataset.i;
        //2:将下标对象数组中元素cb修改当前复选状态
        var checked = e.target.checked;
        //3:将数组中对应cb状态修改
        this.list[idx].cb = checked;
        //4:统计购物车商品选中的数量 == list.length
       //5:将全选状态修改为true否则为 false
        var count = 0;
        for(var item of this.list){
              if(item.cb){
                count++;
              }
        }
        if(count==this.list.length){
           this.allcb = true;
        }else{
           this.allcb = false;
        }
      },
          //删除多个商品
          removeItem(){
             //0:创建空字符串，为了后续接拼字符串
        var html = "";
        //1:遍历数组中元素
        for(var item of this.list){
         //2:判断cb==true
         if(item.cb){
          //3:保存id：拼字符串
          html+=item.id+","}
        }
        console.log(html);
        //3.1 截取字符串
        html = html.substring(0,html.length-1)
        //4:发送ajax请求 删除多个商品
     /*   var url="http://127.0.0.1:3000/"
           url+="removeMItem?ids="+html;
           this.axios.get(url)
           .then(result=>{
               if(result.data.code==1){
                   Toast("删除成功")
                   //使其页面上的显示消失
                   this.loadMore()
               }          })  */
      },

        //删除单个绑定
        delItem(e){
            //e是事件对象
            // 1 获取当前购物车
         var id = e.target.dataset.id
         var idx = e.target.dataset.idx
         console.log(idx)
            // 2 发送ajax请求
      /*      var url="http://127.0.0.1:3000/"
            url+="delCartItem?id="+id
            this.axios.get(url)
            .then(result=>{
                if(result.data.code ==1){
                    Toast("删除成功")
                    this.list.splice(idx,1)
                }
            })
            // 3 获取返回结果判断是否删除成功
            // 4 删除list对应购物车商品对象
*/
        }  
    },
    created(){
        this.loadMore()
    }

}
</script>
<style>

</style>



