import Vue from 'vue'
import Router from 'vue-router'
import HelloContainer from "./components/HelloWorld.vue"
import Home from './components/Home.vue'
import Movie from './components/movie/Movie.vue'
import Login from './components/login/Login.vue'
import MovieInfo from './components/movie/MovieInfo.vue'
import Lifelist from './components/life/lifeList.vue'
import LifeDetail from './components/life/lifeDetail.vue'
import LifeFood from './components/life/lifeFood.vue'
import ShopList from  './components/shop/shopList.vue'
//轮播的导入
Vue.use(Router)

export default new Router({
  routes: [
    {path:'/',component:Home},
    {path:'/Home',component:Home},
    {path:'/Movie',component:Movie},
    {path:'/MovieInfo',component:MovieInfo},
    {path:'/Login',component:Login},
    {path:'/Lifelist',component:Lifelist},
    {path:'/LifeFood',component:LifeFood},
    {path:'/LifeDetail',component:LifeDetail}, 
    {path:'/Shoplist',component:ShopList},
  ]
})



